// 

// axios
import request from '@/utils/request'

// 
/**
 *
 * @param {*} q 
 */
export const getSearchSuggestion = q => {
  return request({
    url: '/v1_0/suggestion',
    method: 'get',
    params: {
      q: q
    }
  })
}

/**
 * 
 */
export const getSearchResult = data => {
  return request({
    url: '/v1_0/search',
    method: 'get',
    params: data
  })
}
