<template>
  <div class="search-history">
    <van-cell title="">
      <div v-if="isEditShow && historyList.length">
        <span @click="delAllHistory"></span>
        <span @click="isEditShow=false"></span>
      </div>
      <van-icon v-else @click="isEditShow=true" name="delete" />
    </van-cell>
    <van-cell @click="onHistoryClick(item,index)" v-for="(item,index) in historyList" :key="index" :title="item">
      <van-icon name="close"/>
    </van-cell>
  </div>
</template>

<script>
export default {
  name: 'SearchHistory',
  components: {},
  props: {
    // 
    historyList: {
      type: Array,
      required: true

    }
  },
  data () {
    return {
      isEditShow: false // 
    }
  },
  computed: {},
  watch: {},
  created () {},
  mounted () {},
  methods: {
    // 
    onHistoryClick (val, index) {
      // 
      if (this.isEditShow) {
        // ,
        this.historyList.splice(index, 1)
      } else {
        // ,
        this.$emit('updateSearchText', val)
      }
    },
    // 
    delAllHistory () {
      this.historyList.splice(0, this.historyList.length)
    }
  }
}
</script>

<style scoped lang="scss"></style>
