<template>
  <div class="search-container">
    <!--  -->
    <!--
      Tips:  van-search  form  action  iOS 
     -->
    <form action="/">
      <van-search
        v-model="searchText"
        show-action
        placeholder=""
        background="#3296fa"
        @search="onSearch"
        @cancel="onCancel"
        @clear="isResultShow=false"
      />
    </form>
    <!-- / -->

    <!--  -->
    <search-result :search-text="searchText" v-if="isResultShow" />
    <!-- / -->

    <!--  -->
    <search-suggestion @updateSearchText='onSearch' :search-text="searchText" v-else-if="searchText" />
    <!-- / -->

    <!--  -->
    <search-history @updateSearchText='onSearch' :history-list=historyList v-else />
    <!-- / -->

  </div>
</template>

<script>
import SearchHistory from './components/search-history'
import SearchSuggestion from './components/search-suggestion'
import SearchResult from './components/search-result'
import { setItem, getItem } from '@/utils/storage'

export default {
  name: 'Search',
  components: {
    SearchHistory,
    SearchSuggestion,
    SearchResult
  },
  props: {},
  data () {
    const historyList = getItem('TOUTIAO_HISTORY_LIST') || []
    return {
      // 
      searchText: '',
      // 
      isResultShow: false,
      historyList: historyList
    }
  },
  computed: {},
  watch: {
    // ,
    historyList: {
      handler () {
        setItem('TOUTIAO_HISTORY_LIST', this.historyList)
      }
    }

  },
  created () {},
  mounted () {},
  methods: {
    // entry
    onSearch (val) {
      // console.log(val)
      // 
      this.searchText = val
      // 
      this.isResultShow = true
      // ()
      if (!this.historyList.includes(val)) {
        // 
        this.historyList.unshift(this.searchText)
      }
    },
    // 
    onCancel () {
      this.$router.back()
    }
    // 
    // onupdateSearchText (val) {
    //   // 
    //   this.searchText = val
    //   // 
    //   this.isResultShow = true
    // }

  }
}
</script>

<style scoped lang="scss">
.search-container {
  .van-search__action {
    color: #fff;
  }
}
</style>
