<template>
  <div class="search-suggestion">
    <van-cell @click="$emit('updateSearchText',item)" v-for="(item,index) in suggestionList" :key="index"  icon="search">
      <div slot="title" v-html="highlight(item)"></div>

    </van-cell>
  </div>
</template>

<script>
import { debounce } from 'lodash'
import { getSearchSuggestion } from '@/api/search'
export default {
  name: 'SearchSuggestion',
  components: {},
  props: {
    // 
    searchText: {
      type: String,
      required: true
    }
  },
  data () {
    return {
      // 
      suggestionList: []
    }
  },
  computed: {},
  watch: {
    // 
    searchText: {
      // handler (newval) {
      //   this.loadSuggestion()
      // },
      handler: debounce(function () {
        this.loadSuggestion()
        // console.log(this.searchText)
      }, 1000),
      // 
      immediate: true
    }
  },
  created () {},
  mounted () {},
  methods: {
    // 
    async loadSuggestion () {
      try {
        const { data: res } = await getSearchSuggestion(this.searchText)
        // console.log(res)
        // 
        this.suggestionList = res.data.options
      } catch (error) {
        this.$toast.fail('')
      }
    },
    // 
    highlight (val) {
      // ,,,
      const reg = new RegExp(this.searchText, 'gi')
      // span
      const newStr = val.replace(reg, `<span style="color:red">${this.searchText}</span>`)
      return newStr
    }
  }
}
</script>

<style scoped lang="less"></style>
