<template>
  <div class="search-result">
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text=""
      @load="onLoad"
    >
      <van-cell v-for="(item,index) in resultlist" :key="index" :title="item.title" />
    </van-list>
  </div>
</template>

<script>
import { getSearchResult } from '@/api/search'
export default {
  name: 'SearchResult',
  components: {},
  props: {
    searchText: {
      type: String,
      require: true
    }
  },
  data () {
    return {
      resultlist: [],
      loading: false,
      finished: false,
      pagenum: 1,
      pagesize: 10
    }
  },
  computed: {},
  watch: {},
  created () {},
  mounted () {},
  methods: {
    async onLoad () {
      // console.log(this.searchText)
      try {
        // 1
        const { data: res } = await getSearchResult({
          page: this.pagenum,
          per_page: this.pagesize,
          q: this.searchText
        })
        console.log(res)
        // 2.
        this.resultlist.push(...res.data.results)
        // 3
        this.loading = false
        // 4
        if (res.data.results.length < this.pagesize) {
          this.finished = true
        } else {
          this.pagenum++
        }
      } catch (error) {
        this.$toast.fail('')
      }
    }
  }

}
</script>

<style scoped lang="scss"></style>
